@import url('https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

.App {
  font-family: 'Karla', sans-serif;
}

button {
  all: initial;
  cursor: pointer;
  font-family: 'Karla', sans-serif;
  color: white;
  font-weight: bold;
  letter-spacing: -0.1rem;
  text-align: center;
  border-radius: 8px;
}

.purple--gradient {
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
}

header {
  height: 7vh;

  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.logo {
  height: 30px;
}

.header--title {
  font-size: 1.3rem;
  color: white;
}

main {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
}

.input--wrapper {
  display: flex;
  gap: 16px;
}

form input {
  width: 230px;
  height: 35px;
  font-size: 0.75rem;
  text-indent: 2ch;
  border: 1px solid gray;
  border-radius: 8px;
  font-family: 'Karla', sans-serif;
}

.new--image--button {
  height: 40px;
}

.meme {
  position: relative;
}

.meme--image {
  width: 477px;
}

.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}
